import { LitElement, css, html } from 'lit';
import { customElement } from 'lit/decorators.js';

var actifReadyNotif = function(){
  alert("Celenganku sudah Aktif!");
  //new Notification("Celenganku sudah Aktif!");
  Notification.requestPermission(function(result) {
    if (result === 'granted') {
      navigator.serviceWorker.ready.then(function(registration) {
        registration.showNotification('Celenganku sudah Aktif!');
      });
    }
  });
  navigator?.serviceWorker?.controller?.postMessage({
    func: 'setNotifAvailable',
    value: true
  });
}

@customElement('app-main')
export class AppMain extends LitElement {
  static get styles() {
    return css`

    fluent-card {
      padding: 0px 18px 18px;
    }

    @media(prefers-color-scheme: light) {
      fluent-card {
        --fill-color: #edebe9;
      }
    }

    @media(prefers-color-scheme: dark) {
      fluent-card {
        --fill-color: #4e4e4e;
        color: white;
        border: none;
      }
    }

    @media (min-width: 1024px) {
      fluent-card {
        width: 54%;
      }
    }
	.avatar {
	  vertical-align: middle;
	  width: 50px;
	  height: 50px;
	  border-radius: 50%;
	}
    `;
  }

  constructor() {
    super();
  }

  async firstUpdated() {
    var mybtn = <HTMLElement> this.shadowRoot?.querySelector('#btnaktifkan');
    console.log(mybtn);
    mybtn?.addEventListener('click', this._notifyMe);

  }

  _notifyMe() {
    console.log("notify me!");
    //let thisobj = this;
    // Let's check if the browser supports notifications
    if (!("Notification" in window)) {
      alert("This browser does not support desktop notification");
    }

    // Let's check whether notification permissions have already been granted
    else if (Notification.permission === "granted") {
      // If it's okay let's create a notification
      //const notification = new Notification("Hi there!");
      //new Notification("Celenganku sudah Aktif!");
      actifReadyNotif();
    }

    // Otherwise, we need to ask the user for permission
    else if (Notification.permission !== "denied") {
      Notification.requestPermission().then(function (permission) {
        // If the user accepts, let's create a notification
        if (permission === "granted") {
          //const notification = new Notification("Celenganku sudah Aktif!");
          //new Notification("Celenganku sudah Aktif!");
          actifReadyNotif();
        }
      });
    }

    // At last, if the user has denied notifications, and you
    // want to be respectful there is no need to bother them any more.
  }

  notifElement(){
    return (("Notification" in window) && Notification.permission === "granted")?html``:html`<fluent-card><button id="btnaktifkan">Aktifkan celenganku</button></fluent-card>`;
  }

  render() {
    return html`
      <app-header ?enableBack="${false}"></app-header>

      <div>
		<h2>Celenganku</h2>
		<fluent-card>
			<center><img alt="App Logo" src="/assets/icons/icon_512.png" style="width: 100%;height: auto;"></center>
			<p>Celenganku sudah diisi <span id="diisicounter">...</span> kali.</p>
		</fluent-card>
    ${this.notifElement()}
    <fluent-card>
      <p>Apps ini disponsori oleh Purwana Networks @ Snack ,<a href="/susunkata">Susun Kata Solver</a>, &amp Aplikasi Mobile</p>
    </fluent-card>

		<!--


        <fluent-card>
          <h2>Did you know?</h2>

          <p>PWAs have access to many useful APIs in modern browsers! These
            APIs have enabled many new types of apps that can be built as PWAs, such as advanced graphics editing apps, games, apps that use machine learning and more!
          </p>

          <p>Check out <fluent-anchor href="https://docs.microsoft.com/en-us/microsoft-edge/progressive-web-apps-chromium/how-to/handle-files">these docs</fluent-anchor> to learn more about the advanced features that you can use in your PWA</p>

        </fluent-card>
		-->
      </div>
    `;
  }
}
